export const getSystemVoices = () => (
	window.speechSynthesis.getVoices()
);

export const getJapanVoice = (voices) => (
	voices.filter((obj) => obj.lang === 'ja-JP')
);

const speakInit = (voice, str) => {
	const speaker = new SpeechSynthesisUtterance();
	speaker.lang = 'ja-JP';
	speaker.volume = 1.0;
	speaker.rate = 0.9;
	speaker.pitch = 1.0;
	speaker.voice = voice;
	speaker.text = str || '';
	return speaker;
};

export default speakInit;



